.container {
  margin: 20px 0 120px 0; }

.flex-column {
  display: flex;
  flex-direction: column; }

.flex-row {
  display: flex;
  flex-direction: row; }

.justify-center {
  justify-content: center; }

.align-center {
  align-items: center; }

.align-start {
  align-items: start; }

.space-around {
  justify-content: space-around; }

.width-max-content {
  width: max-content; }

.large-button {
  margin: 20px 5px 20px 5px;
  padding: 0.5rem 1rem;
  font-size: 1.25rem;
  border-radius: 0.3rem;
  display: inline-block;
  font-weight: 400;
  line-height: 1.5;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  background-color: white;
  color: orange;
  z-index: 1; }

.small-button {
  margin: 0 0 0 4px;
  padding: 1px 4px;
  border-radius: 18px;
  background-color: orange;
  color: white; }

.submit-button {
  background-color: white;
  color: orange; }

.header {
  margin: 48px 24px 24px 24px;
  font-family: Simpsons;
  font-size: 80px; }

.quote {
  font-family: "Simpsons";
  font-size: 24px;
  max-width: 800px;
  border-radius: 25px;
  padding: 20px;
  background-color: white;
  opacity: 0.75; }

.quote-image {
  height: 400px;
  width: auto; }

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
  padding: 20px;
  border-radius: 25px;
  background-color: orange;
  color: white;
  max-width: 40em; }

.increment-decrement-button {
  background-color: orange;
  color: white; }

.list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: left;
  margin: 60px 10px 10px 10px;
  padding: 10px;
  border-radius: 25px;
  min-height: 200px;
  min-width: 200px;
  width: max-content;
  background-color: white; }

.list h2 {
  align-self: center;
  margin-bottom: 8px; }

.count {
  margin: 24px;
  font-size: 60px; }

.challenge-container {
  position: relative;
  width: 100%; }

.constraints {
  position: absolute;
  align-self: start;
  width: 100%;
  z-index: 1; }

.hints {
  position: relative;
  z-index: 2; }

